<template>
    <section class="page db-design">
        <div class="db-sidebar">
            <ul>
                <li>
                    <a href="javascript:;" title="Cursor" @click="selectedTool = 'move'">
                        <img src="/assets/lambda/images/db/cursor.svg" alt="Cursor">
                    </a>
                </li>
                <li>
                    <a href="javascript:;" title="new table" @click="selectedTool = 'new-table'">
                        <img src="/assets/lambda/images/db/table.svg" alt="new TABLE">
                    </a>
                </li>
            </ul>
        </div>
        <div class="db-workspace">
            <div id="db-designer" @click="clickEvent">
                <Diagram :model="model" :class="`${selectedTool}`"
                         ref="diagram"
                         v-if="editorH >= 1 && editorW >= 1"
                         :width="editorW"
                         :height="editorH"
                         :onUpdatedCTM="onUpdatedCTM"
                         @selectTable="selectTable"
                />
            </div>
        </div>
        <Drawer
            class="table-option"
            :title="`${selectedTable.title} - Table`"
            v-model="showTableOption"
            width="720"
            :mask-closable="false">
            <div v-if="selectedTableIndex >= 0">
                <Input v-model="selectedTable.title" :placeholder="common.table_name"/>
                <Tabs value="Columns">
                    <TabPane label="Columns" name="Columns">
                        <Columns :columns="selectedTable.ports" @addColumn="addColumn" :index="selectedTableIndex"></Columns>
                    </TabPane>
                    <TabPane label="Indexes" name="Indexes">Indexes</TabPane>
                    <TabPane label="Foreign keys" name="ForeignKeys">Foreign keys</TabPane>
                    <TabPane label="Triggers" name="Triggers">Triggers</TabPane>
                </Tabs>
                <div class="drawer-footer">
                    <Button style="margin-right: 8px" @click="showTableOption = false">{{ common.cancel }}</Button>
                    <Button type="primary" @click="saveTableOption">{{ common.save }}</Button>
                </div>
            </div>
        </Drawer>
    </section>
</template>

<script>


const emptyTableOption = {"title":"","x":0,"y":0,"width":0,"height":0,"ports":[]}
import {
    Diagram
} from "./diagram/index";
import Columns from "./components/Columns"
    export default {
        components: {
            Diagram,
            Columns
        },

        data() {
            const model = new Diagram.Model();
            return {
                model: model,
                editorW: 0,
                editorH: 0,
                showTableOption:false,
                rootZoom:0,
                selectedTool:"move",
                selectedTableIndex:-1,
                selectedTable:{...emptyTableOption}

            };
        },
        mounted() {
            this.createDiagram();
        },
        computed:{
            common() {
                const labels = ['cancel', 'save', 'table_name'];
                return labels.reduce((obj, key, i) => {
                    obj[key] = this.$t('role.' + labels[i]);

                    return obj;
                }, {});
            },
        },
        methods: {


            addColumn(index){
                let column = {
                    name: "new_columnd",
                    dbType: "int",
                    alias: "new_columnd",
                    alias_db: "new_columnd",
                    output: false,
                    sortType: "none",
                    sortOrder: 1,
                    groupBy: false,
                    groupOrder: 1,
                    aggregate: "none",
                    color: null,
                    table: this.model._model.nodes[index].title
                };

                this.selectedTable.addInPort(column, this.selectedTable.ports.length-1);
                this.selectedTable.addOutPort(column, this.selectedTable.ports.length-1);
                this.selectedTable.height = this.selectedTable.ports.length * 20 + 50;
            },
            selectTable(index){
                this.selectedTableIndex = index;
                this.selectedTable = _.cloneDeep(this.model._model.nodes[index]);
                this.showTableOption = true;
                console.log(this.selectedTable);
            },
            onUpdatedCTM(e){
                this.rootZoom = e.d;

            },

            clickEvent(e){
                if(this.selectedTool == "new-table"){
                    this.addTable(e);
                }
            },

            addTable(e){
                let postition = this.$refs.diagram.convertXYtoViewPort(e.clientX, e.clientY-(10*this.rootZoom))                //
                this.model.addNode(
                    "table",
                    postition.x,
                    postition.y,
                    160,
                    50
                );
                this.selectedTool = "move";
            },

            createDiagram() {
                const editorDOM = document.getElementById("db-designer");

                this.editorW = editorDOM.clientWidth;
                this.editorH = editorDOM.clientHeight ;


            },
            saveTableOption(){
                // this.model._model.nodes[this.selectedTableIndex] = {...this.selectedTable};
                Vue.set(this.model._model.nodes, this.selectedTableIndex, {...this.selectedTable});
                Vue.set(this.$data, "showTableOption", false);
            }
        },
        watch:{
            showTableOption(value){
                if(!value){
                    this.selectedTableIndex = -1;
                    this.selectedTable = {...emptyTableOption};
                }
            }
        }

    };
</script>
