<template>
    <div class="db-table">
        <Row class="db-table-header">
            <Col span="10"> {{ lang.column }}</Col>
            <Col span="6"> {{ lang.dataType }}</Col>
            <Col span="2" class="center"> PK</Col>
            <!--ID -->
            <Col span="2" class="center"> NN</Col>
            <!-- -->
            <Col span="2" class="center"> UQ</Col>
            <!---->
            <Col span="2" class="center"> AI</Col>
            <!-- -->
        </Row>
        <div class="db-table-body">
            <Column v-for="col in columns" :key="col.index" :column="col" v-show="col.type == 'in'"></Column>
        </div>
        <Row>
            <Col>
                <Button type="success" shape="circle" icon="md-add" @click="addColumn">{{lang.AddColumn}}</Button>
                <Divider />
            </Col>
        </Row>
    </div>
</template>

<script>
import Column from "./ColumnOptions"

export default {
name: "Columns",
    components:{
        Column
    },
    props:["columns", "index"],
    methods:{
        addColumn(){
            this.$emit("addColumn", this.index);

        },
    },
    computed:{
        lang() {
            const labels = ['AddColumn', 'column', 'dataType'];
            return labels.reduce((obj, key, i) => {
                obj[key] = this.$t('adminModule.' + labels[i]);
                return obj;
            }, {});
        },
    }
}
</script>

